import request from '@/utils/request';
export function getItem(id, query) {
  return request({
    url: "/api/v0/items/".concat(id),
    method: 'get',
    params: query
  });
}
export function findItems(query) {
  return request({
    url: '/api/v0/items',
    method: 'get',
    params: query
  }).then(function (_ref) {
    var data = _ref.data;
    return data;
  });
}
export function newItem(item) {
  return request({
    url: '/api/v0/items',
    method: 'post',
    data: item
  });
}
export function updateItem(item) {
  return request({
    url: "/api/v0/items/".concat(item._id),
    method: 'put',
    data: item
  });
}
export function itemImageUrl(item) {
  return "".concat(process.env.VUE_APP_BASE_API, "/api/v0/items/").concat(item._id, "/image");
}